	import { CF } from '../../utils/cfApi'
	const cf = new CF()
	export default {
		data() {
			return {
				listData: [], // 接收数据
				womanId: '' // 用户编号
			}
		},
		onLoad(options) {
			this.womanId = options.womanId;
		},
		onShow(){
			this.GetMyCollection()
		},
		methods: {
			
			// 获取收藏数据
			GetMyCollection() {
				uni.showLoading({
					title:'加载中...',
					mask: true
				})
				const data = {
					womanId: this.womanId
				}
				cf.GetMyCollection(data).then((res)=>{
					  this.listData= res.dtData
					  uni.hideLoading()
				})
			},
			
			// 处理时间
			treatTime (date) {
                if(date){
                    var dateArr =  date.split(' ');
                    return dateArr[0]
                }
			},
			
			//跳转详情页
			jumpDetal(data) {
				uni.navigateTo({
					url: `../../pages/Web/share?WomanId=${data.UserId}&articleId=${data.ArticleId}` ,
				})
			}
		}
	}
